<template>
  <div class="clearfix header-wrap" ref="wrapper">
    <ul id="content">
      <li
        v-for="(item, index) in labels"
        :key="index"
      ><router-link
        :to="'/forum/list/' + item.label_id">{{item.cn}}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Forum-New-Header',
  props: {
    labels: Array
  },
  mounted () {
  }
}
</script>

<style lang="less" scoped>
  .header-wrap{
    position: fixed;
    width: 100%;
    z-index: 50;
    height: 55px;
    font-size: 16px;
    background-color: #fff;
    overflow: hidden;
    overflow-x: scroll;
    -webkit-overflow-x: scroll;
    border-bottom: 1px solid #ddd;
    border-top: 1px solid #ddd;
    background-color: cornflowerblue;
    -webkit-overflow-scrolling: touch;
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      position: absolute;
      top: 0;
      left: 0;
      height: 55px;
      z-index: 1;
      width: 1000px;
      transition-property: -webkit-transform;
      transform-origin: 0px 0px 0px;
      transform: translate3d(0px, 0px, 0px) scale(1);
      li{
        float: left;
        height: 55px;
        line-height: 55px;
        text-align: center;
        position: relative;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        padding: 0 10px;
        a {
          font-size: 18px;
          font-weight: 600;
          color: #fff;
        }
      }
    }
  }
</style>



