<template>
  <div class="forum-main-wrap">
    <div class="forum-penal">
      <div class="title">社区置顶</div>
      <div class="main">
        <forum-main-posts-item
          v-for="(item, index) in postsList.top"
          :key="index"
          :posts="item"
        ></forum-main-posts-item>
      </div>
    </div>
    <div class="forum-penal">
      <div class="title">社区精华</div>
      <div class="main">
        <forum-main-posts-item
          v-for="(item, index) in postsList.highlight"
          :key="index"
          :posts="item"
        ></forum-main-posts-item>
      </div>
    </div>
    <div class="forum-penal">
      <div class="title">最新帖子</div>
      <div class="main">
        <forum-main-posts-item
          v-for="(item, index) in postsList.new"
          :key="index"
          :posts="item"
        ></forum-main-posts-item>
      </div>
    </div>
    <div class="forum-penal">
      <div class="title">社区最热</div>
      <div class="main">
        <forum-main-posts-item
          v-for="(item, index) in postsList.hot"
          :key="index"
          :posts="item"
        ></forum-main-posts-item>
      </div>
    </div>
    <div class="forum-penal">
      <div class="title">精彩推荐</div>
      <div class="main" style="min-height: 20rem; padding: 1rem;">
        <mt-swipe :auto="3000" style="height: 20rem">
          <mt-swipe-item
            v-for="(item, index) in ad"
            :key="index">
            <img :src="item.imgurl" alt="" style="width: 100%; height: 100%;">
          </mt-swipe-item>
        </mt-swipe>
      </div>
    </div>
  </div>
</template>

<script>
import ForumMainPostsItem from './forum-main-posts-item'
export default {
  name: 'Forum-Main',
  props: {
    postsList: Object,
    ad: ''
  },
  components: {
    ForumMainPostsItem
  }
}
</script>

<style lang="less" scoped>
  .forum-main-wrap{
    width: 100%;
    position: absolute;
    padding-top: 40px;
    overflow: hidden;
    background-color: #e5e5e5;
    &:after{
      display: block;
      width: 100%;
      height: 5rem;
      font-size: .8rem;
      content: '我也是有底线的...';
      border-bottom: 1px solid #ddd;
      color: #999;
      text-align: center;
    }
    .forum-penal {
      margin-top: .6rem;
      border: 1px;
      border-color: #e5e6e9 #dfe0e4 #d0d1d5;
      margin-bottom: 15px;
      box-shadow: 0 1px 1px rgba(0,0,0,0.05);
      background-color: #fff;
      .title {
        background: #fafafa;
        padding: 6px 15px;
        border-bottom-color: #eee;
        color: #777;
        border-color: #ddd;
        border-bottom: 1px solid transparent;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
      }
    }
  }
</style>



