<template>
  <div class="posts-item">
    <div class="infos">
      <div class="title">
        <router-link :to="'/forum/posts/' +posts.forum_id">{{posts.title}}</router-link>
      </div>
      <div class="info">
        <router-link :to="'/forum/home/' + posts.user_id">{{posts.user_name}}</router-link>
        from
        <router-link :to="'/forum/list/' + posts.label_id">{{posts.label_name}}</router-link>
      </div>
      <div class="info">
        {{posts.browse}}次浏览,{{posts.reply_amount}}次回复
      </div>
    </div>
    <div class="count" v-show="posts.img">
      <img :src="posts.img" alt="">
    </div>
  </div>
</template>

<script>
export default {
  name: 'Forum-Main-Posts-Item',
  props: {
    posts: Object
  }
}
</script>

<style lang="less" scoped>
  .posts-item {
    display: flex;
    border-bottom: 0px;
    min-height: 68px;
    border-bottom: 1px solid #F0F0F0;
    padding: .5rem 1.5rem;
    margin: 0 -15px;
    vertical-align: top;
    .infos {
      flex:1;
      .title {
        overflow: hidden;
        a {
          font-size: .8rem;
          color: #222;
          font-weight: 400;
          line-height: 30px;
        }
      }
      .info {
        color: #ABA8A6;
        font-size: 12px;
        margin-top: 0;
        a {
          color: #ABA8A6;
        }
      }
    }
    .count{
      max-height: 3.5rem;
      img{
        width: 100%;
        height:100%;
        max-width: 7rem;
      }
    }
  }
</style>



