<template>
  <div class="forum-wrap">
    <!--<forum-header-->
      <!--:labels="datas.label"-->
    <!--&gt;</forum-header>-->
    <forum-new-header
      :labels="datas.label"
    ></forum-new-header>
    <forum-main
      :postsList="datas.list"
      :ad="ads"
    ></forum-main>
  </div>
</template>

<script>
import ForumHeader from './forum-header'
import ForumNewHeader from './forum-new-header.vue'
import ForumMain from './forum-main'
import {getForumList, getForumAds} from '../../api/forum'
import {pushIntention} from '../../api/maintain'
import {Toast, Indicator} from 'mint-ui'
import * as types from '../../store/mutation-types'
export default {
  name: 'Forum',
  data () {
    return {
      datas: '',
      ads: ''
    }
  },
  created () {
    Indicator.open({
      text: '加载中...'
    })
    this.getDatas()
  },
  beforeRouteLeave (to, from, next) {
    Indicator.close()
    next()
  },
  methods: {
    getDatas () {
      pushIntention(undefined, 4, 2)
      getForumList()
        .then(data => {
          if (data.state) {
            this.$set(this, 'datas', data.data)
            this.$store.commit(types.SET_FORUM_LABEL, data.data.label)
            Indicator.close()
          } else {
            Toast(data.errormsg || '获取数据失败')
          }
        }).catch(err => console.log(err))
      getForumAds()
        .then(data => {
          if (data.state) {
            this.$set(this, 'ads', data.data.list)
          } else {
            Toast(data.errormsg || '获取数据失败')
          }
        })
    }
  },
  components: {
    ForumHeader,
    ForumMain,
    ForumNewHeader
  }
}
</script>

<style lang="less" scoped>
  .forum-wrap{
    height: 100%;
    background-color: #e5e5e5;
  }
</style>



